import '../css/special.styl';
import { h, render } from 'preact';
import { Provider, connect } from 'preact-redux';
import App from './components/app';
import store from './store';
import * as Analytics from "./lib/analytics";

const IMAGES = [];

export default class Special {
  constructor(params = {}) {
    this.params = params;
    this.container = this.params.container;

    if (this.params.css) {
      this.loadStyles(this.params.css).then(() => this.init());
    } else {
      this.init();
    }
  }

  loadStyles(path) {
    return new Promise((resolve, reject) => {
      let link = document.createElement('link');

      link.rel = 'stylesheet';
      link.href = path;

      link.onload = () => resolve();
      link.onerror = () => reject();

      document.body.appendChild(link);
    });
  }

  addEvents() {
    this.container.addEventListener('click', (e) => {
      if (e.target.tagName.toLowerCase() === 'a') {
        Analytics.sendEvent(e.target.href);
      }
    });
  }

  init() {
    this.addEvents();
    const Special = () => (
      <Provider store={store}>
        <App />
      </Provider>
    );

    render(<Special />, this.container);

    store.dispatch({
      type: 'TEST_PARAMS',
      params: this.params,
    });

    const images = [
      {
        img: 'https://leonardo.osnova.io/6b760a9e-6b97-a76c-4851-05bcf62b0b90/',
      },
      {
        img: 'https://leonardo.osnova.io/660e597f-8048-d340-57ee-3d818d55e7dd/',
        img2x: 'https://leonardo.osnova.io/22f8cd8d-5341-2a3b-2763-e34ba74106cd/',
      },
      {
        img: 'https://leonardo.osnova.io/72e2b127-9cf8-3976-c4c2-b2f0f14eca1e/',
        img2x: 'https://leonardo.osnova.io/0c94ee87-6e20-32bc-eff6-1583e715e6cc/',
      },
      {
        img: 'https://leonardo.osnova.io/1306e7b6-e62e-16d6-45a8-2c5f952bf96c/',
        img2x: 'https://leonardo.osnova.io/bdbb58d8-0a8d-393b-8c40-403ee9f6ce94/',
      }
    ];

    images.forEach(item => {
      let img = document.createElement('img');
      img.src = item.img;
      if (item.img2x) {
        img.srcset = item.img2x + ' 2x';
      }
      IMAGES.push(img);
    });

    console.log(IMAGES);
  }
}